import React, { Component } from 'react';
import predictions from 'root/constants/predictions';
import predictionOptions from 'root/constants/predictionOptions';
import Bubble from './Bubble';

const WIDTH = 349;
const HEIGHT = 370;
const PIX_RATIO = .2;
const INVERT_RATIO = 1 / PIX_RATIO;
const pixSize = [WIDTH * PIX_RATIO, HEIGHT * PIX_RATIO];

const bubbleSpecs = [
  {
    differences: [
      {
        id: 'female',
        relativeDifference: .3
      },
      {
        id: 'age',
        relativeDifference: .3
      },
      {
        id: 'intelligence',
        relativeDifference: .3
      },
      {
        id: 'satisfaction_life',
        relativeDifference: .3
      },
      {
        id: 'politics',
        relativeDifference: .3
      },
      {
        id: 'religion',
        relativeDifference: .3
      },
      {
        id: 'big5',
        relativeDifference: .3
      }
    ],
    colored: false,
    isUser: true,
    x: WIDTH * .5 * PIX_RATIO,
    y: HEIGHT * .61 * PIX_RATIO,
    r: 7
  },
  {
    differences: [
      {
        id: 'female',
        relativeDifference: .2
      },
      {
        id: 'age',
        relativeDifference: .3
      },
      {
        id: 'intelligence',
        relativeDifference: .1
      },
      {
        id: 'satisfaction_life',
        relativeDifference: .4
      },
      {
        id: 'politics',
        relativeDifference: .2
      },
      {
        id: 'religion',
        relativeDifference: .2
      },
      {
        id: 'big5',
        relativeDifference: .2
      }
    ],
    colored: true,
    x: WIDTH * .55 * PIX_RATIO,
    y: HEIGHT * .9 * PIX_RATIO,
    r: 3
  },
  {
    differences: [
      {
        id: 'female',
        relativeDifference: .9
      },
      {
        id: 'age',
        relativeDifference: .8
      },
      {
        id: 'intelligence',
        relativeDifference: .9
      },
      {
        id: 'satisfaction_life',
        relativeDifference: .9
      },
      {
        id: 'politics',
        relativeDifference: .7
      },
      {
        id: 'religion',
        relativeDifference: .9
      },
      {
        id: 'big5',
        relativeDifference: 1
      }
    ],
    colored: true,
    x: WIDTH * .41 * PIX_RATIO,
    y: HEIGHT * .1 * PIX_RATIO,
    r: 6
  }
]

class BubblesLegend extends Component {
  componentDidMount() {
    const ctx = this.canvas.getContext('2d');
    ctx.mozImageSmoothingEnabled = false;
    ctx.webkitImageSmoothingEnabled = false;
    ctx.imageSmoothingEnabled = false;
    this.updateBubbles();
  }

  componentDidUpdate() {
    this.updateBubbles();
  }

  updateBubbles() {
    const { canvas } = this;
    const ctx = canvas.getContext('2d');
    const selectedGroups = this.getSelectedGroups();

    ctx.fillStyle = "#fafafa";
    ctx.rect(0, 0, pixSize[0], pixSize[1]);
    ctx.fill();
    bubbleSpecs.forEach(bubbleSpec => 
      Bubble(ctx, bubbleSpec)
        .update({selectedGroups, colored: true})
        .render()
    )
    ctx.drawImage(canvas, 0, 0, pixSize[0], pixSize[1], 0, 0, WIDTH, HEIGHT);

    ctx.strokeStyle = "#888";
    ctx.setLineDash([2, 4]);
    ctx.beginPath();
    ctx.moveTo(bubbleSpecs[0].x * INVERT_RATIO - 8, bubbleSpecs[0].y * INVERT_RATIO - 48);
    ctx.lineTo(bubbleSpecs[2].x * INVERT_RATIO + 5, bubbleSpecs[2].y * INVERT_RATIO + 25);
    ctx.moveTo(bubbleSpecs[0].x * INVERT_RATIO + 10, bubbleSpecs[0].y * INVERT_RATIO + 49);
    ctx.lineTo(bubbleSpecs[1].x * INVERT_RATIO - 3, bubbleSpecs[1].y * INVERT_RATIO - 28);
    ctx.stroke();
  }

  getSelectedGroups() {
    return predictionOptions.filter(group =>
      this.props.predictionsState.filter(prediction => 
        group.properties.indexOf(prediction.id) !== -1 && prediction.value
      ).length
    );
  }

  render() {
    return (
      <div className="bubbles-legend">
        <canvas
          width={WIDTH}
          height={HEIGHT}
          className="bubbles-legend__canvas" ref={el => this.canvas = el}/>
        <div 
          style={{
            left: bubbleSpecs[2].x * INVERT_RATIO + 45, 
            top: bubbleSpecs[2].y * INVERT_RATIO - 15
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-lead">Less<br/>similar</h4>
        </div>
        <div 
          style={{
            left: bubbleSpecs[1].x * INVERT_RATIO + 40, 
            top: bubbleSpecs[1].y * INVERT_RATIO - 15
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-lead">More<br/>similar</h4>
        </div>
        <div 
          style={{
            right: WIDTH - (bubbleSpecs[2].x * INVERT_RATIO - 48),
            top: bubbleSpecs[2].y * INVERT_RATIO - 23,
            textAlign: "right"
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-title">1.Size</h4>
          <p className="bubbles-legend__textbox-copy">Overall<br/>similarity</p>
        </div>
        <div 
          style={{
            left: bubbleSpecs[2].x * INVERT_RATIO + 40, 
            top: bubbleSpecs[2].y * INVERT_RATIO + 60
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-title">2.Distance</h4>
          <p className="bubbles-legend__textbox-copy">Similarity in selected predictions</p>
        </div>
        <div 
          style={{
            right: WIDTH - bubbleSpecs[1].x * INVERT_RATIO + 45, 
            top: bubbleSpecs[1].y * INVERT_RATIO - 30,
            textAlign: "right"
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-title">3.Color intensity</h4>
          <p className="bubbles-legend__textbox-copy">Similarity in individual prediction</p>
        </div>
        <div 
          style={{
            right: WIDTH - bubbleSpecs[0].x * INVERT_RATIO + 50,
            top: bubbleSpecs[0].y * INVERT_RATIO - 15,
            textAlign: "right"
          }}
          className="bubbles-legend__textbox">
          <h4 className="bubbles-legend__textbox-lead">Your<br/>Bubble</h4>
        </div>
      </div>
    )
  }
};

export default BubblesLegend;


